// import E from 'wangeditor'
export default{
  data () {
    const validatePassCheck = (rule, value, callback) => {
      if (value === '') {
          callback(new Error(''));
      } else if (value !== this.ruleForm.password) {
          callback(new Error('!'));
      } else {
          callback();
      }
    };
    return {
      ruleValidate: {
        username: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            max: 7,
            message: "",
            trigger: "blur",
          }
        ],
        password1: [
          { validator: validatePassCheck, trigger: 'blur' }
        ],
        catename: [
          {
            type: "string",
            required: true,
            message: ",",
            trigger: "blur"
          },
          {
            type: "string",
            max: 7,
            message: ",",
            trigger: "blur"
          }
        ],
        email: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          { type: "email", message: "", trigger: "blur" }
        ],
        address: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        sex: [{ required: true, message: "", trigger: "change" }],
        age: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        password: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            type: "string",
            min: 6,
            message: ",",
            trigger: "blur"
          }
        ]
      }
    }
  }
}